import React from 'react';
import style from './styles/index.module.scss'
import Icon from '../Icon';

const Wrapper = ({ children, isPreview=false, image }) => {
  return <div 
  style={{
    '--slider-grid': isPreview? '1fr' : '75px 1fr'
  }}
  className={style['slider__wrapper']}>
    <div
      className={style['slider__wrapper-preview-container']}
    >
      <Icon
        src={image}
        height={550}
        width={isPreview? 350 : 550}
      />
    </div>
    {children}
    </div>;
};

export default Wrapper;
