import React from 'react';
import style from './styles/index.module.scss';
import Icon from '../Icon';

const FirstSliderWrapper = ({ children, image }) => {
  return (
    <div className={style['first-slider__wrapper']}>       
      <div className={style['first-slider__gallery--vertical']}>
        {children}</div>
    </div>
  );
};

export default FirstSliderWrapper;
